body {
  margin: 0;
  font-family: "Roboto";
}

.Room {
  width: 100vw;
  height: 100vh;
  display: grid;
  background: #AAA;
  grid-gap: 1px;
  grid-template-columns: 300px auto;
  grid-template-rows: 100px auto 100px;
  grid-template-areas: "chat topbar" "chat game" "chat brush";
}
.Room > * {
  background: white;
}
.Room .icon-button {
  background: #60A6FF;
  color: white;
  border: none;
  margin: 10px;
  width: 80px;
  border-radius: 5px;
  border: 2px solid #60A6FF;
}
.Room .icon-button:hover {
  -webkit-filter: brightness(0.9);
          filter: brightness(0.9);
}
.Room .icon-button:active {
  -webkit-filter: brightness(0.8);
          filter: brightness(0.8);
}
.Room .icon-button:focus {
  outline: none;
}
.Room .icon-button span {
  font-size: 48px;
  margin: auto;
}
.Room .tool-button {
  background: white;
  color: #60A6FF;
}
.Room .tool-button:first-of-type {
  margin-right: 0;
  border-right: 0;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.Room .tool-button:nth-of-type(2) {
  margin-left: 0;
  border-left: 0;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.Room .tool-button.active {
  background: #60A6FF;
  color: white;
}
.Room #game {
  display: inherit;
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  grid-area: game;
}
.Room #game #canvasGame {
  width: 100%;
  height: 100%;
}
.Room #game #choices {
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(3, 8, 29, 0.8);
  align-items: center;
  justify-content: space-evenly;
}
.Room #game #choices .choice {
  background: white;
  border: 0;
  font-size: 2em;
  padding: 5px;
}
.Room #game #choices .choice:hover {
  -webkit-filter: brightness(0.9);
          filter: brightness(0.9);
}
.Room #game #choices .choice:active {
  -webkit-filter: brightness(0.8);
          filter: brightness(0.8);
}
.Room #game #choices .choice:focus {
  outline: none;
}
.Room #brush {
  grid-area: brush;
  display: flex;
}
.Room #brush #colours {
  margin: auto 10px;
  -webkit-column-count: 10;
          column-count: 10;
}
.Room #brush #colours .colour {
  display: inline-block;
  width: 25px;
  height: 25px;
  margin: 1.5px;
  border: 1px solid #0000004D;
  border-radius: 5px;
}
.Room #brush #colours .colour.active {
  border-radius: 50%;
}
.Room #brush #size {
  display: flex;
  justify-content: center;
  align-items: center;
}
.Room #brush #size .inner label {
  display: block;
  color: #777;
  text-align: center;
  font-family: Roboto Slab;
  font-size: 18px;
  font-weight: bold;
}
.Room #brush #size .inner [type=range] {
  -webkit-appearance: none;
  margin: 11px 0;
  width: 100%;
}
.Room #brush #size .inner [type=range]:focus {
  outline: 0;
}
.Room #brush #size .inner [type=range]:focus::-webkit-slider-runnable-track {
  background: #7ab4ff;
}
.Room #brush #size .inner [type=range]:focus::-ms-fill-lower {
  background: #60A6FF;
}
.Room #brush #size .inner [type=range]:focus::-ms-fill-upper {
  background: #7ab4ff;
}
.Room #brush #size .inner [type=range]::-webkit-slider-runnable-track {
  cursor: pointer;
  height: 8px;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  width: 100%;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.2), 0 0 0 rgba(13, 13, 13, 0.2);
  background: #60A6FF;
  border: 0 solid #cfd8dc;
  border-radius: 5px;
}
.Room #brush #size .inner [type=range]::-webkit-slider-thumb {
  background: white;
  border: 1px solid #aaa;
  border-radius: 50%;
  cursor: pointer;
  height: 22px;
  width: 22px;
  -webkit-appearance: none;
  margin-top: -7px;
}
.Room #brush #size .inner [type=range]::-moz-range-track {
  cursor: pointer;
  height: 8px;
  -moz-transition: all 0.2s ease;
  transition: all 0.2s ease;
  width: 100%;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.2), 0 0 0 rgba(13, 13, 13, 0.2);
  background: #60A6FF;
  border: 0 solid #cfd8dc;
  border-radius: 5px;
}
.Room #brush #size .inner [type=range]::-moz-range-thumb {
  background: white;
  border: 1px solid #aaa;
  border-radius: 50%;
  cursor: pointer;
  height: 22px;
  width: 22px;
}
.Room #brush #size .inner [type=range]::-ms-track {
  cursor: pointer;
  height: 8px;
  -ms-transition: all 0.2s ease;
  transition: all 0.2s ease;
  width: 100%;
  background: transparent;
  border-color: transparent;
  border-width: 11px 0;
  color: transparent;
}
.Room #brush #size .inner [type=range]::-ms-fill-lower {
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.2), 0 0 0 rgba(13, 13, 13, 0.2);
  background: #4798ff;
  border: 0 solid #cfd8dc;
  border-radius: 10px;
}
.Room #brush #size .inner [type=range]::-ms-fill-upper {
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.2), 0 0 0 rgba(13, 13, 13, 0.2);
  background: #60A6FF;
  border: 0 solid #cfd8dc;
  border-radius: 10px;
}
.Room #brush #size .inner [type=range]::-ms-thumb {
  background: white;
  border: 1px solid #aaa;
  border-radius: 50%;
  cursor: pointer;
  height: 22px;
  width: 22px;
  margin-top: 0;
}
.Room #chat {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  grid-area: chat;
  overflow: hidden;
}
.Room #chat #players {
  margin: 0;
  padding-left: 0;
  list-style-type: none;
}
.Room #chat #players .player {
  display: flex;
  height: 40px;
  border-width: 1px 0;
  border-style: solid;
  border-color: #AAA;
  margin-top: -1px;
}
.Room #chat #players .player * {
  display: inline-block;
  margin: auto 0;
}
.Room #chat #players .player .player-icon {
  color: #60A6FF;
  font-size: 2em;
  margin: auto 10px;
}
.Room #chat #players .player .player-name {
  color: #222;
  font-weight: 600;
}
.Room #chat #players .player .player-points {
  color: #60A6FF;
  font-weight: 600;
  margin-left: auto;
  margin-right: 10px;
}
.Room #chat #messages {
  display: flex;
  flex-direction: column-reverse;
  flex: 1 1;
  overflow-y: auto;
  font-size: 16px;
  padding-bottom: 5px;
}
.Room #chat #messages .message {
  padding: 2px 0;
}
.Room #chat #messages .message strong {
  color: #777;
}
.Room #chat #messages .message.reveal {
  color: #60A6FF;
  font-weight: 600;
}
.Room #chat #messages .message.guessed {
  color: mediumseagreen;
  font-weight: 600;
}
.Room #chat #messages .message.close {
  color: goldenrod;
  font-weight: 600;
}
.Room #chat #messages::-webkit-scrollbar {
  height: 100%;
  width: 10px;
}
.Room #chat #messages::-webkit-scrollbar-thumb {
  background: #60A6FF;
  border-radius: 50px;
}
.Room #chat #messages::-webkit-scrollbar-track {
  background: white;
}
.Room #chat #message-input {
  height: 50px;
  width: 100%;
  border-color: #AAA;
  border-style: solid;
  border-width: 1px 0 0 0;
}
.Room #chat #message-input .input {
  width: auto;
  height: 100%;
  overflow: hidden;
}
.Room #chat #message-input .input input {
  height: 100%;
  width: 100%;
  font-size: 17px;
  border: 0;
  padding: 1px 10px;
  box-sizing: border-box;
}
.Room #chat #message-input .input input:focus {
  outline: 0;
}
.Room #chat #message-input .button {
  width: 50px;
  height: 50px;
  float: right;
}
.Room #chat #message-input .button button {
  height: 100%;
  width: 100%;
  line-height: 50px;
  border-radius: 0;
  border: 0;
  background: #60A6FF;
  color: white;
}
.Room #chat #message-input .button button span {
  line-height: 50px;
}
.Room #chat #chatbox input {
  width: 100%;
  box-sizing: border-box;
}
.Room #topbar {
  grid-area: topbar;
  position: relative;
  display: flex;
  justify-content: center;
}
.Room #topbar > div {
  display: inline-block;
  height: 100px;
}
.Room #topbar #timer {
  position: absolute;
  left: 20px;
  line-height: 100px;
  vertical-align: top;
  text-align: center;
  font-weight: bold;
  font-size: 2em;
  color: #444;
}
.Room #topbar #hint {
  position: absolute;
  left: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.Room #topbar #hint .category {
  background: #60A6FF;
  border-radius: 50px;
  color: white;
  padding: 5px;
}
.Room #topbar #hint :empty {
  display: none;
}
.Room #topbar #to-draw {
  width: 250px;
  height: 100px;
  background: #60A6FF;
  position: relative;
  z-index: 2;
  transition: height 0.2s ease-in-out;
  overflow: hidden;
  font-weight: 500;
}
.Room #topbar #to-draw #word {
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Room #topbar #to-draw #word span {
  background: white;
  padding: 10px;
  border-radius: 50px;
  color: #60A6FF;
  margin: auto 5px;
  min-width: 50px;
  text-align: center;
}
.Room #topbar #to-draw #word-info {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.Room #topbar #to-draw #word-info > * {
  margin: 10px auto;
}
.Room #topbar #to-draw #word-info img {
  max-width: 230px;
}
.Room #topbar #to-draw #word-info #category {
  background: white;
  padding: 10px;
  margin-top: 0;
  border-radius: 50px;
  color: #60A6FF;
}
.Room #topbar #to-draw #word-info #subtext {
  color: white;
  margin-top: 0;
}
.Room #topbar #to-draw #word-info #subtext:empty {
  display: none;
}
.Room #topbar #to-draw #word-info #pin {
  position: absolute;
  background: transparent;
  border: none;
  right: 5px;
  bottom: 0;
}
.Room #topbar #to-draw #word-info #pin span {
  font-size: 18px;
  color: white;
  transition: transform 0.1s ease-in;
}
.Room #topbar #to-draw #word-info #pin.on span {
  transform: rotate(45deg);
}
.Room #topbar #to-draw #word-info #pin:hover {
  -webkit-filter: brightness(0.9);
          filter: brightness(0.9);
}
.Room #topbar #to-draw #word-info #pin:active {
  -webkit-filter: brightness(0.8);
          filter: brightness(0.8);
}
.Room #topbar #to-draw #word-info #pin:focus {
  outline: none;
}

